<template>
    <div class="wrap-nav effi-wrap">
      <Header :htitle = "msg" :changew="msgb" :icoS="leftIco" :icob="rightIco" :icobS="icoRS" :icoSS="icoLS"></Header>
      <div class="top">
        <div class="b-left">
          <p class="h-2">数据类型</p>
          <div class="switchtag">
            <span class="on">在线</span>
            <span>填报</span>
            <b></b>
          </div>
        </div>
        <div class="b-right">
          年份选择
        </div>
      </div>
      <div class="con">
          <div class="mid-a">
            <p class="h-4"><img src="../assets/hico.png">能源数据看板</p>
            <ul class="">
              <li v-for="(itema,index) in energyData" v-bind:key="index">
                <p class="t-name">{{ itema.name }}</p>
                <p class="t-num">{{ itema.num }}</p>
                <p class="t-unit">{{ itema.unit }}</p>
                <p class="t-note">同比:{{ itema.note }}%</p>
              </li>
            </ul>
          </div>
          <div class="mid-b">
              <p class="h-4"><img src="../assets/hico.png">区域指标排名</p>
              <div class="chart">
                  <div class="charttop">
                    <p>单位：tce</p>
                    <div class="seletbox">
                      <select>
                        <option selected>总量</option>
                        <option>水</option>
                        <option>电</option>
                        <option>煤</option>
                        <option>油</option>
                        <option>气</option>
                        <option>其他能源</option>
                      </select>
                      <span><img src="../assets/eff/select.png"></span>
                    </div>
                  </div>
                  <highcharts-container :defOptions="optionsa" style="width: 100%;height: 3.333333rem;"></highcharts-container>
              </div>
          </div>
          <div class="mid-c">
            <p class="h-4"><img src="../assets/hico.png">区域能耗产值分析</p>
            <div class="chart">
              <div class="charttop">
                <p>单位:tce</p>
                <div>
                  综合能耗<img src="../assets/eff/leg1.png">
                  产值能耗<img src="../assets/eff/leg2.png">
                </div>
                <p>单位:kgce/万元</p>
              </div>
              <highcharts-container :defOptions="options" style="width: 100%;height: 4.333333rem;"></highcharts-container>
            </div>
          </div>
          <div class="bot">
            <p class="h-4"><img src="../assets/hico.png">指标趋势变化</p>
            <table>
              <tr>
                <th>排名</th>
                <th>区域名称</th>
                <th>能源消费<br>总量(tce)</th>
                <th>占比</th>
                <th>综合能耗<br>(tce)</th>
                <th>工业总产<br>值(万元)</th>
                <th>万元产值<br>单耗</th>
              </tr>
              <tr v-for="(itemb,index) in dataList">
                <td>{{ itemb.id }}</td>
                <td style="color: #2dded4;">{{ itemb.name }}</td>
                <td>{{ itemb.numa }}</td>
                <td>{{ itemb.numb }}</td>
                <td>{{ itemb.numc }}</td>
                <td>{{ itemb.numd }}</td>
                <td>{{ itemb.nume }}</td>
              </tr>
            </table>
          </div>
      </div>
    </div>
  </template>

  <script>
    import Header from '@/components/part/Header'
    import HighchartsContainer from '../components/part/chart'

    export default {
      name: 'efficiency',
      data() {
        return {
          //顶部标题
          msg: '区域分析',//标题
          msgb: '', //右侧显示文字
          icoLS: true, //左边图标是否
          leftIco: 'backI', //closeI 关闭
          icoRS: true, //左边图标是否
          rightIco: 'plusI', // wordl 文字
          //临时数组
          energyData: [
            {id: 0, name: '全社会能源消费总量', num: 1234, unit: 'tec', note: 29},
            {id: 1, name: '能源消费总量', num: 123, unit: 'tec', note: -29},
            {id: 2, name: '工业用电量', num: 123, unit: '万kw/H', note: 29},
            {id: 3, name: '工业总产值', num: 123, unit: '亿元', note: 29},
            {id: 4, name: '产值单耗', num: 1234, unit: 'kgce/万元', note: -29},
          ],
          dataList: [
            {id: 1, name: '莱城区', numa: 25789.16, numb: 12, numc: 4391.18, numd: 2070.4, nume: 248.45},
            {id: 2, name: '钢城区', numa: 25789.16, numb: 12, numc: 4391.18, numd: 2070.4, nume: 248.45},
            {id: 3, name: '经济开发区', numa: 25789.16, numb: 12, numc: 4391.18, numd: 2070.4, nume: 248.45},
            {id: 4, name: '辛庄镇', numa: 25789.16, numb: 12, numc: 4391.18, numd: 2070.4, nume: 248.45},
            {id: 5, name: '苗山镇', numa: 25789.16, numb: 12, numc: 4391.18, numd: 2070.4, nume: 248.45}
          ],
          //区域能源消费品种排行
          optionsa:{
            chart: { //基本信息
              type: 'column',
              backgroundColor: '#062f43',
              style: {
                color: '#ffffff'
              }
            },
            title: {
                text: null
              },
            legend: { //图例
              enabled: false
            },
            xAxis: { //X轴
              categories: ['任城区','兖州区','任城区','任城区','任城区','任城区'],
              lineColor: '#09435f', //轴线颜色
              tickPixelInterval: 5,
              //crosshair: true,
              labels: {
                style: {
                  color: '#e1e1e1',
                  fontSize: ".266667rem"
                }
              }
            },
            yAxis: {
              min: 0,
              max: 1000,
              gridLineColor: '#09435f', //网格线颜色
              tickAmount: 6,
              tickPixelInterval: 35,
              title: {
                text: null
              },
              labels: {
                style: {
                  color: '#e1e1e1',
                  fontSize: ".266667rem"
                }
              },
              stackLabels: {  // 堆叠数据标签
                enabled: false,
              }
            },
            tooltip: {
              // head + 每个 point + footer 拼接成完整的 table
              headerFormat: '<span style="font-size:10px;color:#333;">{point.key}</span><table>',
              pointFormat: '<tr><td style="color:#333;padding:0">{series.name}: </td>' +
              '<td style="padding:0;color:#333;">{point.y:.1f}</td></tr>',
              footerFormat: '</table>',
              shared: true,
              useHTML: true
            },
            plotOptions: {
              column: {
                stacking: 'normal',
                dataLabels: { // 数据列上是否显示数值
                  enabled: false
                },
                pointWidth: 15
              },
              series: {
                borderColor: 'none'
              }
            },
            series: [ //数据
              {name: '气', color: '#f3cd7a', data: [200, 200, 175, 130, 60, 50]},
              {name: '油', color: '#def37a', data: [200, 170, 155, 60, 80, 85]},
              {name: '煤', color: '#55eea8', data: [210, 120, 125, 130, 150, 140]},
              {name: '电', color: '#48d6f0', data: [50, 150, 50, 120, 100, 210]},
              {name: '水', color: '#6d9af5', data: [230, 200, 180, 190, 280, 80]}
            ],
            credits: { //版权信息
              enabled: false
            },
            exporting: { //导出按钮
              enabled: false
            }
          },
          //区域能耗产值分析
          options:{
            chart: { //基本信息
              type: 'column',
              backgroundColor: '#062f43',
              style: {
                color: '#ffffff'
              }
            },
            title: {
                text: null
              },
            legend: { //图例
              enabled: false
            },
            xAxis: { //X轴
              categories: [
                '任城区','兖州区','微山县','鱼台县','金乡县','嘉祥县','汶上县','泗水县','梁山县','曲阜市'
              ],
              lineColor: '#09435f', //轴线颜色
              tickPixelInterval: 50,
              //crosshair: true,
              labels: {
                style: {
                  color: '#ffffff',
                  fontSize: ".266667rem"
                }
              }
            },
            yAxis: [
              {
                min: 0,
                max: 1000,
                gridLineColor: '#09435f', //网格线颜色
                tickAmount: 6,
                tickPixelInterval: 35,
                title: {
                  text: null
                },
                labels: {
                  style: {
                    color: '#fff',
                    fontSize: ".266667rem"
                  }
                }
              }, {
                min: 0,
                max: 300,
                gridLineColor: '#09435f', //网格线颜色
                tickAmount: 6,
                tickPixelInterval: 35,
                title: {
                  text: null
                },
                labels: {
                  style: {
                    color: '#fff',
                    fontSize: ".266667rem"
                  }
                },
                opposite: true
              }],
            tooltip: {
              // head + 每个 point + footer 拼接成完整的 table
              headerFormat: '<span style="font-size:10px;color:#333;">{point.key}</span><table>',
              pointFormat: '<tr><td style="color:#333;padding:0">{series.name}: </td>' +
              '<td style="padding:0;color:#333;">{point.y:.1f}</td></tr>',
              footerFormat: '</table>',
              shared: true,
              useHTML: true
            },
            plotOptions: {
              column: {
                borderWidth: 0
              },
              series: {
                borderRadius: 5
              }
            },
            series: [{ //数据
              name: '',
              color: {
                linearGradient: { x1: 0, x2: 0, y1: 0, y2: 1 },
                stops: [
                    [0, '#44cee8'],
                    [1, '#9480e1']
                ]
              },
              data: [620, 780, 630, 890, 770, 900, 600, 499, 789, 695,]
            },{
              name: '',
              color: {
                linearGradient: { x1: 0, x2: 0, y1: 0, y2: 1 },
                stops: [
                    [0, '#80e1be'],
                    [1, '#48d6f0']
                ]
              },
              data: [500, 600, 499, 789, 695, 700, 600, 768, 585, 500]
            }],
            credits: { //版权信息
              enabled: false
            },
            exporting: { //导出按钮
              enabled: false
            }
          },

        }
      },
      components: {
        Header,
        HighchartsContainer,
      }
    }
  </script>

  <style lang="less" scoped>
    .effi-wrap {}
    .top {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 .333333rem;
      margin-bottom: .333333rem;
      .b-left {
        display: flex;
        align-items: center;
      }
      .b-right {
        font-size: .4rem;
      }
      .h-2 {
        font-weight: bold;
        margin-right: .2rem;
        font-size: .4rem;
      }
    }
    .con {
      .mid-a {
        background: #062f43;
        padding: 0 .333333rem;
        .h-4 {
          border-bottom: 1px solid #13435b;
        }
        ul {
          li {
            display: flex;
            align-items: center;
            padding: .2rem .133333rem;
            border-bottom: 1px solid #13435b;
            p {
              font-size: .293333rem;
            }
            .t-name {
              color: #2dded4;
              width: 40%;
            }
            .t-num {
              width: 20%;
              font-family: digital;
              color: #f8e177;
              font-size: .346667rem;
            }
            .t-unit {
              width: 20%;
            }
            .t-note {
              width: 20%;
              text-align: right;
            }
          }
        }
      }
      .mid-b {
        background: #062f43;
        padding: 0 .333333rem;
        margin: .2rem 0;
        .chart {
          .charttop {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: .2rem;
            p {
              font-size: .266667rem;
            }
            .seletbox {
              position: relative;
              span {
                display: block;
                position: absolute;
                top: 1px;
                right: 0;
                z-index: 5;
                img {
                  width: .453333rem;
                  height: .44rem;
                }
              }
            }
            select{
              width: 2.4rem;
              height: .466667rem;
              padding-left: .133333rem;
              border: 1px solid #2dded4;
              font-size: .266667rem;
              background: none;
              position: relative;
              z-index: 10;
              option {
                background: #1a5876;
              }
            }
          }
        }
      }
      .mid-c {
        background: #062f43;
        padding: 0 .333333rem;
        margin: .2rem 0;
        .chart {
          .charttop {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: .266667rem;
            p {
              font-size: .266667rem;
            }
            div {
              font-size: .266667rem;
              img {
                margin: 0 .133333rem;
              }
            }
          }
        }
      }
      .bot {
        padding: 0 .333333rem .266667rem .333333rem;
        background: #062f43;
        table {
          width: 100%;
          text-align: center;
          th {
            background: #2dded4;
            color: #fff;
            font-size: .32rem;
          }

          td {
            font-size: .266667rem;
            padding: .12rem 0;
            background: #13435b;
          }
        }
      }
    }
  </style>



